import React, {Component} from 'react';
import {connect} from 'react-redux';
import './css/FooterBar.css';
import { thisTypeAnnotation } from '@babel/types';



class FooterBar extends Component {

    getHeight = () =>{
        if (this.props.CardHeight > 0){
            return {};
        } else {
            return {display:'none'}
        }
    }

    render() {
        return (
            <div style={this.getHeight()} className="FooterBar">
                <ul>
                    <li>
                       <p>  2019 Daniel Nahle </p>
                    </li>
                </ul>
            </div>
        );
    }
} 

const mapStateToProps = ({CardHeight}) => {
    return {CardHeight}
}

export default connect(mapStateToProps)(FooterBar);
